$icon-font-url: '~fonts/uncanny-learndash-groups-icons';

@font-face {
	font-family: 'Uncanny LearnDash Groups Icons';
	src:
		url( $icon-font-url + '.ttf' ) format( 'truetype' ),
		url( $icon-font-url + '.woff' ) format( 'woff' ),
		url( $icon-font-url + '.svg' ) format( 'svg' );
		font-weight: normal;
		font-style: normal;
}

@mixin icons-font {
	font-family: 'Uncanny LearnDash Groups Icons' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.ulg-icon {
	@include icons-font;

	&--angle-down:before {
		content: $icon-angle-down;
	}

	&--angle-left:before {
		content: $icon-angle-left;
	}

	&--angle-right:before {
		content: $icon-angle-right;
	}

	&--angle-up:before {
		content: $icon-angle-up;
	}

	&--check:before {
		content: $icon-check;
	}

	&--lock-alt:before {
		content: $icon-lock-alt;
	}

	&--times:before {
		content: $icon-times;
	}

	&--spinner-third:before {
		content: $icon-spinner-third;
	}

	&--download:before {
		content: $icon-download;
	}

	&--print:before {
		content: $icon-print;
	}

	&--pencil:before {
		content: $icon-pencil;
	}

	&--file-contract:before {
		content: $icon-file-contract;
	}

	&--file-download:before {
		content: $icon-file-download;
	}

	&--file-export:before {
		content: $icon-file-export;
	}

	&--file-import:before {
		content: $icon-file-import;
	}

	&--search:before {
		content: $icon-search;
	}

	&--search-plus:before {
		content: $icon-search-plus;
	}

	&--search-minus:before {
		content: $icon-search-minus;
	}

	&--info:before {
		content: $icon-info;
	}
}