/* General */

.uo-groups {
	font-size: 15px;
}

.uo-groups * {
	box-sizing: border-box;
}

.uo-groups a {
	text-decoration: none;
}

.uo-groups .uo-row {
	width: 100%;
}

	.uo-groups .uo-row:after {
		content: '';
		display: table;
		clear: both;
	}

	.uo-groups .uo-row-checkbox {
		display: flex;
		align-items: center;
	}

		.uo-groups .uo-row .uo-row-checkbox input {
			width: auto;
			height: auto;
			margin-top: 0;
		}

		.uo-groups .uo-row-checkbox__label {
			padding-left: 7px;
		}

.uo-groups .uo-left {
	float: left;
}

.uo-groups .uo-center {
	margin: 0 auto;
}

.uo-groups .uo-right {
	float: right;
}

.uo-groups select,
.uo-groups ul li {
	font-family: inherit;
}

.uo-groups .uo-groups-normal-margin {
	margin-top: 15px;
}

.uo-groups .uo-groups-small-margin {
	margin-top: 15px;
}

@media (min-width: 769px){
	.show-in-mobile-only {
		display: none !important;
	}
}

/* Loading */

.uo-modal-spinner {
	width: 30px;
	height: 30px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	z-index: 99999999;
	display: none;

	background: url('data:image/gif;base64,') #111 no-repeat center center;

	background-size: 80%;

	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	border-radius: 5px;
}

/* Tooltip */

.uo-groups *[data-tooltip] {
	position: relative;
}

.uo-groups *[data-tooltip]:before {
	content: attr(data-tooltip);
	position: absolute;
	background: rgba(0,0,0,.8);

	min-width: 150px;
	max-width: 200px;

	top: -20px;
	left: 50%;
	transform: translateX(-50%);

	font-size: 13px;

	color: #fff;
	padding: 3px 8px;

	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	-ms-border-radius: 3px;
	-o-border-radius: 3px;
	border-radius: 3px;
}

/* Monospace */

.uo-groups .uo-looks-like-code {
	font-family: 'Lucida Console', Monaco, monospace;
	letter-spacing: 1px;
}

/* Headings */

.uo-groups h1, .uo-groups .uo-looks-like-h1,
.uo-groups h2, .uo-groups .uo-looks-like-h2,
.uo-groups h3, .uo-groups .uo-looks-like-h3,
.uo-groups h4, .uo-groups .uo-looks-like-h4,
.uo-groups h5, .uo-groups .uo-looks-like-h5,
.uo-groups h6, .uo-groups .uo-looks-like-h6 {
	color: #191919;
	font-weight: bold;
	margin: 0;
}

.uo-groups h1, .uo-groups .uo-looks-like-h1 { font-size: 40px; }
.uo-groups h2, .uo-groups .uo-looks-like-h2 { font-size: 30px; }
.uo-groups h3, .uo-groups .uo-looks-like-h3 { font-size: 25px; }
.uo-groups h4, .uo-groups .uo-looks-like-h4 { font-size: 22px; }
.uo-groups h5, .uo-groups .uo-looks-like-h5 { font-size: 18px; }
.uo-groups h6, .uo-groups .uo-looks-like-h6 { font-size: 15px; }

.uo-groups .uo-header-subtitle {
	margin-top: 5px;
}

	.uo-groups .uo-subtitle-of-h1 { font-size: 30px; }
	.uo-groups .uo-subtitle-of-h2 { font-size: 22px; }
	.uo-groups .uo-subtitle-of-h3 { font-size: 16px; }
	.uo-groups .uo-subtitle-of-h4,
	.uo-groups .uo-subtitle-of-h5,
	.uo-groups .uo-subtitle-of-h6 { font-size: 15px; }

	.uo-groups .uo-header-subtitle .uo-btn {
		margin-left: 5px;
	}

/* Paragraphs */

.uo-groups p {
	margin: 0 0 10px 0;
}

/* Buttons */

.uo-groups .uo-btn {
	font-size: 15px;

	border-color: #b7b7b7;
	border-width: 1px;
	border-style: solid;

	border-radius: 3px;

	padding: 6px 12px;

	line-height: 1.4;

	background: #f9fafb;
	transition-duration: 100ms;
	transition-timing-function: linear;
	transition-property: color, border-color;

	text-transform: none;
	font-weight: normal;
	font-family: inherit;
	letter-spacing: 0;

	color: #000;
	cursor: pointer;
	text-decoration: none;

	display: flex;
	align-items: center;
}

	.uo-groups .uo-btn:hover,
	.uo-groups .uo-btn.uo-btn--selected {}

	.uo-groups .uo-btn.uo-btn--small {
		font-size: 12px;
		padding: 5px 10px;
	}

	.uo-groups .uo-btn.uo-btn-arrow-left:before {
		content: $icon-angle-left;
		@include icons-font;
		margin-right: 8px;
	}

@media (max-width: 768px){
	.uo-groups .uo-btn {
		padding: 5px 10px;
	}
}

/* Input */

.uo-groups .uo-input {
	padding: 6px 15px;

	border-color: #b7b7b7;
	border-width: 1px;
	border-style: solid;

	font-family: inherit;

	border-radius: 3px;

	background: #fff;

	width: 100%;
}

	.uo-groups .uo-input.uo-input--big {
		padding: 8px 20px;
	}

	.uo-groups .uo-input:focus {}

.uo-groups .uo-input-icon {
	position: relative;
}

	.uo-groups .uo-input-icon .uo-input-the-icon {
		font-size: 13px;
		position: absolute;
		z-index: 1;
		top: 50%;
		left: 15px;
		transform: translateY(-50%);
	}

	.uo-groups .uo-input-icon .uo-input {
		padding-left: 35px;
	}

/* Filters */

.uo-groups .dataTables_filter {}

	.uo-groups .dataTables_filter input {
		margin: 0;
	}

/* Select */

.uo-groups .uo-select {
	position: relative;
	display: inline-block;
}

	.uo-groups .uo-select button,
	.uo-groups .uo-select select {
		cursor: pointer;

		border-color: #b7b7b7;
		border-width: 1px;
		border-style: solid;

		font-size: 15px;

		border-radius: 3px;

		padding: 6px 15px;

		transition-duration: 100ms;
		transition-timing-function: linear;
		transition-property: color, border-color;

		background: #fff;
	}

		.uo-groups .uo-select button:after {
			content: $icon-angle-down;
			@include icons-font;
			margin-left: 5px;
		}

		.uo-groups .uo-select:hover button,
		.uo-groups .uo-select:hover select {}

			.uo-groups .uo-select select option {
				color: #000;
			}

				.uo-groups .uo-select select option:hover,
				.uo-groups .uo-select select option:checked {}

		.uo-groups .uo-select-modal {
			border-color: #b7b7b7;
			border-width: 1px;
			border-style: solid;

			display: none;

			border-radius: 3px;

			position: absolute;
			background: #fff;
			width: 200px;
			min-width: 100%;
			top: calc(100%);
			left: 50%;
			transform: translateX(-50%);
		}

			.uo-groups .uo-select-modal .uo-select-modal-option {
				padding: 6px 15px;
				width: 100%;
				cursor: context-menu;
			}

				.uo-groups .uo-select-modal .uo-select-modal-option:hover,
				.uo-groups .uo-select-modal .uo-select-modal-option.uo-select-option--selected {}

	.uo-groups .uo-select-jplist {
		position: relative;
	}

		.uo-groups .uo-select-jplist .jplist-drop-down ul {
			width: 100%;
			position: absolute;
			left: 0;
			margin-left: 0;
			z-index: 10;
		}

		.uo-groups .uo-select-jplist .jplist-dd-panel {
			cursor: pointer;

			border-color: #b7b7b7;
			border-width: 1px;
			border-style: solid;

			font-size: 15px;

			border-radius: 3px;

			padding: 6px 15px;
			line-height: 1.3;

			transition-duration: 100ms;
			transition-timing-function: linear;
			transition-property: color, border-color;

			background: #f9fafb;
		}

			.uo-groups .uo-select-jplist .jplist-dd-panel:hover {}

			.uo-groups .uo-select-jplist .jplist-drop-down .jplist-dd-panel:after {
				content: $icon-angle-down;
				@include icons-font;
				margin-left: 8px;
				margin-top: 2px;
			}

			.uo-groups .uo-select-jplist .jplist-dd-panel {
				display: inline-flex;
				align-items: center;
			}

			.uo-groups .uo-select-jplist .uo-select-dropdown {
				border-color: #b7b7b7;
				border-width: 1px;
				border-style: solid;

				text-align: left;

				display: block;

				border-radius: 3px;

				width: 200px;

				position: absolute;
				background: #fff;
				min-width: 100%;
				top: calc(100% + 20px);
				left: 0;
			}

				.uo-groups .uo-select-jplist .uo-select-dropdown li {
					padding: 3px 10px;
					width: 100%;
					cursor: context-menu;
					list-style: none;
				}

				.uo-groups .uo-select-jplist .uo-select-dropdown li.active,
				.uo-groups .uo-select-jplist .uo-select-dropdown li:hover {}

				.uo-groups .uo-select-jplist .uo-select-arrow {
					display: block;
				}

					.uo-groups .uo-select-jplist .uo-select-arrow:before {
						content: '';
						z-index: 10;
						top: calc(100% + 21px);
						left: 50%;
						position: absolute;
						width: 0;
						height: 0;
						border: 7.5px solid #fff;
						border-color: #fff #fff transparent transparent;
						transform-origin: 0 0;
						transform: rotate(-45deg);
						margin-left: -10px;
						box-shadow: 1px -1px 0 0 #b7b7b7;
					}

/* Lists */

.uo-groups .uo-list {
	padding: 0;
	margin: 0;
}

	.uo-groups .uo-list li {
		list-style: none;
		padding: 5px 0 5px 30px;
		position: relative;

		transition-duration: 100ms;
		transition-timing-function: linear;
		transition-property: padding;
	}

		.uo-groups .uo-list li:before {
			content: '';
			width: 20px;
			height: 2px;
			background: #000;

			display: block;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;

			transition-duration: 100ms;
			transition-timing-function: linear;
			transition-property: width;

			-webkit-border-radius: 100px;
			-moz-border-radius: 100px;
			-ms-border-radius: 100px;
			-o-border-radius: 100px;
			border-radius: 100px;
		}

			.uo-groups .uo-list li:hover:before {
				width: 25px;
			}

		.uo-groups .uo-list li:hover {
			padding-left: 35px;
		}

/* Table */

