	.uo-groups .uo-groups-table:hover::-webkit-scrollbar { width: 9px; height: 9px; margin-bottom: 0; }
	.uo-groups .uo-groups-table:hover::-webkit-scrollbar-thumb { background: #9d9d9d; }

