.uo-groups .uo-groups-table {}

	.uo-groups .ulg-hidden-data {
		display: none !important;
	}

	.uo-groups .dataTables_filter {
		width: 100%;
		float: left;
		margin: 10px 0;
	}

		.uo-groups .dataTables_filter label {
			margin: 0;
		}

		.uo-groups .dataTables_filter input {
			width: 100%;
			font-size: inherit;
			font-family: inherit;
			padding: 8px 12px;
			border: 1px solid #bbb;
			background: #fff;
		}

	.uo-groups .dataTables_length {}

		.uo-groups .dataTables_length select {
			height: auto;
			margin: 0 5px;
		}

	.uo-groups .uo-groups-table th {
		border-bottom: 0;
		font-weight: 500;
		line-height: 1.4;
		font-size: 15px;
		height: auto;
		vertical-align: middle;
		padding: 10px 10px;
		border: 0;
	}

	.uo-groups .uo-groups-table td {
		padding: 10px 10px;
		line-height: 1.4;
		vertical-align: top;
	}

	.uo-groups .uo-groups-table th label,
	.uo-groups .uo-groups-table td label {
		margin: 0;
	}

	.uo-groups .uo-groups-table td {
		font-size: 14px;
	}

	.uo-groups .uo-groups-table td a,
	.uo-groups .uo-groups-table .user_edit_link {
		text-decoration: none;
		border-bottom: 0;
		cursor: pointer;
	}

	.uo-groups .uo-groups-table .essay_approve_single,
	.uo-groups .uo-groups-table .assignment_approve_single {
		font-weight: 400;
		font-size: 14px;
		padding: 8px;
	}
	
	.uo-groups .uo-groups__cell-checkbox {
		text-align: center;
	}

	.uo-groups-datatable-header {
		display: flex;
		justify-content: flex-end;
	}

		.uo-groups-datatable-header .dt-button {
			font-size: 15px;
			border: 1px solid #b7b7b7;
			border-radius: 3px;
			padding: 6px 12px;
			line-height: 1.4;
			background: #f9fafb;
			text-transform: none;
			font-weight: 400;
			font-family: inherit;
			color: #000;
			cursor: pointer;
			text-decoration: none;
			display: inline-block;
		}

		.uo-groups-datatable-header .dt-button:not(:last-child){
			margin-right: 10px;
		}

	.uo-groups-datatable-footer {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 15px;
	}

		.uo-groups-datatable-footer .dataTables_length label {
			margin-bottom: 0;
		}

		.uo-groups-datatable-footer .dataTables_info,
		.uo-groups-datatable-footer .dataTables_paginate {
			padding-top: 0;
		}

		.uo-groups-datatable-footer .dataTables_length {
			white-space: nowrap;
		}

		.uo-groups-datatable-footer .dataTables_paginate {
			padding-left: 15px;
			padding-right: 15px;

			text-align: center;

			/* display: flex; */
			/* align-items: flex-start; */
			/* justify-content: center; */
			/* flex-wrap: wrap; */
		}

		.uo-groups-datatable-footer .paginate_button {
			font-size: 15px;
			border: 1px solid #b7b7b7;
			border-radius: 3px;
			padding: 3px 12px;
			line-height: 1.2;
			background: #f9fafb;
			color: #000;
			display: inline-block;
			cursor: pointer;
			white-space: nowrap;

			margin-bottom: 10px;
		}

			.uo-groups-datatable-footer .dataTables_paginate > span,
			.uo-groups-datatable-footer .dataTables_paginate > .paginate_button.next {
				margin-left: 7px;
			}

			.uo-groups-datatable-footer .dataTables_paginate > span {
				display: inline;
			}

			.uo-groups-datatable-footer .dataTables_paginate > span > .paginate_button:not(:first-child) {
				margin-left: 7px;
			}

	@media screen and (max-width: 768px){
		.uo-groups-datatable-footer {
			flex-wrap: wrap;

			.dataTables_length,
			.dataTables_paginate,
			.dataTables_info {
				width: 100%;
			}

			.dataTables_paginate,
			.dataTables_info {
				padding-top: 10px;
			}
		}
	}

	.uo-table-datatable.collapsed tbody tr td:first-child {
		padding-left: 30px !important;
	}

.dataTables-filter-columns {
	width: 100%;

	float: left;

	display: flex;
	flex-wrap: wrap;

	align-items: center;

	user-select: none;
}

	.dataTables-filter-columns-field {
		display: none !important;
	}

	.dataTables-filter-columns__fields {
		display: none;
	}

	.dataTables-filter-columns__toggle,
	.dataTables-filter-columns__fields {
		width: 100%;
	}

		.dataTables-filter-columns__toggle a {}

		.dataTables-filter-columns__fields label {
			padding-right: 10px;
		}

			.dataTables-filter-columns__fields label input {
				margin-right: 5px;
			}

	.dataTables-filter-columns-field:checked ~ .dataTables-filter-columns__fields {
		display: block;
	}

	.dataTables-filter-columns__toggle label {
		display: inline-block;

		color: #0000ee;

		cursor: pointer;
	}

		.dataTables-filter-columns__toggle label:hover {
			text-decoration: underline;
		}

	.dataTables-filter-columns-field:not(:checked) ~ .dataTables-filter-columns__toggle label:before {
		content: attr(data-label-enable);
	}

	.dataTables-filter-columns-field:checked ~ .dataTables-filter-columns__toggle label:before {
		content: attr(data-label-disable);
	}

	table.uo-table-datatable {
		border-collapse: collapse;
	}

		table.uo-table-datatable.dataTable tbody tr:nth-child(2n), 
		table.uo-table-datatable.dataTable tbody tr:nth-child(2n) td,
		table.uo-table-datatable.dataTable tbody tr:nth-child(2n):hover, 
		table.uo-table-datatable.dataTable tbody tr:nth-child(2n) td:hover,
		table.uo-table-datatable.dataTable tbody tr:hover {
			background: #fff;
		}

		table.uo-table-datatable.dataTable.display tbody tr.odd > .sorting_1,
		table.uo-table-datatable.dataTable.order-column.stripe tbody tr.odd > .sorting_1 {
			background: #f6f6f6;
		}

		table.uo-table-datatable.dataTable.display tbody tr.even > .sorting_1,
		table.uo-table-datatable.dataTable.order-column.stripe tbody tr.even > .sorting_1 {
			background: #f6f6f6;
		}