@use 'sass:math';

.ulg-manage-progress {
	$class: '.ulg-manage-progress';

	font-family: $font_family;
	font-size: $dashboard-global-font-size;
	color: $color-dashboard-font-color;

	line-height: 1.4;

	box-sizing: border-box;

	* {
		box-sizing: border-box;
	}

	&-box {
		border: 1px solid $color-dashboard-border;
		border-radius: $dashboard-border-radius;
	}

	&-btn {
		font-size: $dashboard-button-font-size;
		color: $color-dashboard-button-color;

		border: 1px solid $color-dashboard-button-border;
		border-radius: $dashboard-border-radius;
		padding: $dashboard-button-padding;

		line-height: 1;
		text-decoration: none;
		cursor: pointer;

		display: inline-block;

		@include normal-transition( 'border-color' );

		&:hover {
			border-color: $color-dashboard-button-border-hover;
		}

		& + & {
			margin-left: 7px;
		}
	}

	&-toolbar {
		width: 100%;

		display: flex;
		justify-content: flex-end;

		padding-bottom: $dashboard-space-between-actions-and-box;

		#{ $class }-filters {
			flex: 1 1 auto;

			min-width: 0px;

			padding-right: $dashboard-space-between-actions-and-box;

			&-form {
				display: flex;

				#{ $class }-filter {
					&__label {
						font-weight: 400;
						margin-bottom: 6px;
					}

					select {
						font-family: inherit;
						font-size: inherit;
						font-style: inherit;

						max-width: 100%;
					}

					&:not(:last-child) {
						margin-right: math.div( $dashboard-space-between-actions-and-box, 2 );
					}
				}
			}	
		}

		#{ $class }-actions {
			flex: 0 0 auto;

			display: flex;
			align-items: flex-end;
			justify-content: flex-end;

			#{ $class }-btn {
				margin-left: 0;

				&:not(:last-child) {
					margin-right: math.div( $dashboard-space-between-actions-and-box, 2 );
				}
			}
		}
	}

	&-course {
		$section_class: &;

		&__row {
			display: flex;

			border-top: 1px solid $color-dashboard-inner-border;

			#{ $section_class }__toggle-btn {
				border-right: 1px solid $color-dashboard-inner-border;
				width: $dashboard-toggle-button-width;

				font-size: $dashboard-course-name-font-size;

				color: $color-dashboard-toggle-font-color;

				flex: 0 0 auto;

				position: relative;

				@include normal-transition( 'background, color' );

				background: $color-dashboard-toggle-disabled;

				#{ $section_class }--has-lessons &,
				#{ $section_class }--has-quizzes & {
					background: $color-dashboard-toggle-bg;
					cursor: pointer;
				}

				#{ $section_class }--has-lessons &:before,
				#{ $section_class }--has-quizzes &:before {
					@include icons-font;
					content: $icon-angle-down;
					font-size: $dashboard-toggle-button-icon-size;

					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%);
				}

				#{ $section_class }--expanded &,
				#{ $section_class }--expanding & {
					background: $color-dashboard-toggle-expanded-bg;
					border-right: $color-dashboard-toggle-expanded-bg;

					&:before {
						content: $icon-angle-up;
						color: $color-dashboard-toggle-expanded-font-color;
					}
				}

				/* Mobile */
				#{ $class }[max-width~="#{ $dashboard-small-breakpoint }"] & {
					width: $dashboard-toggle-button-width-mobile;
				}
			}

			#{ $section_class }__details {
				flex: 1 1 auto;
				display: flex;
				min-width: 0;

				align-items: center;

				#{ $section_class }__left {
					width: 100%;
					flex: 1 1 auto;
					min-width: 0;

					display: flex;
					align-items: center;

					padding-left: $dashboard-space-between-elements;
					padding-right: $dashboard-space-between-elements;

					#{ $section_class }__progress-actions {
						display: inline-flex;

						padding-right: $dashboard-space-between-elements;

						font-size: $dashboard-button-font-size;

						#{ $section_class }__progress-action-checkbox {
							max-width: $dashboard-checkbox-width;
							padding: $dashboard-checkbox-padding;

							line-height: 1;

							border: 1px solid $color-dashboard-checkbox-border;
							border-radius: $dashboard-border-radius;

							background: #fff;

							cursor: pointer;

							overflow: hidden;

							position: relative;

							#{ $section_class }--can-not-manage-progress & {
								cursor: default;
							}

							#{ $section_class }--disabled & {
								pointer-events: none;
							}

							#{ $section_class }--disabled &:after {
								cursor: default;
							}

							&:before {
								content: '';

								position: absolute;
								top: 50%;
								left: 50%;
								transform: translate(-50%,-50%);

								#{ $section_class }--loading & {
									opacity: 0;
								}
							}

							&:after {
								position: absolute;
								top: 50%;
								left: 50%;
								transform: translate(-50%,-50%);

								opacity: 0;
								transition: opacity 150ms ease-in-out;

								font-size: 11px;
								color: $color-dashboard-checkbox-icon-color;

								content: $icon-spinner-third;
								@include animation-rotate( 'checkbox-doing-call', 1s, 0deg, 365deg, -50%, -50% );
								@include icons-font;
							}

							#{ $section_class }--loading &:after {
								opacity: 1;
							}

							/**
							
							#{ $section_class }--in-progress & {
								border-color: $color-dashboard-checkbox-in-progress;

								&:before {
									width: $dashboard-checkbox-in-progress-width;
									height: 2px;

									background: $color-dashboard-checkbox-in-progress;

									border-radius: 150px;
								}
							}

							*/

							#{ $section_class }--completed & {
								border-color: $color-dashboard-checkbox-completed;

								&:before {
									@include icons-font;
									content: $icon-check;

									font-size: $dashboard-checkbox-icon-size;

									color: $color-dashboard-checkbox-completed;
								}
							}

							#{ $section_class }--loading & {
								border-color: $color-dashboard-checkbox-border;
							}
						}

						#{ $section_class }__progress-action-confirm-text {
							opacity: 0;
							white-space: nowrap;
						}

						#{ $section_class }__progress-action-cancel {
							padding: $dashboard-checkbox-padding;

							margin-left: math.div( $dashboard-space-between-elements, 2 );

							display: none;
						}

						&--need-to-confirm {
							#{ $section_class }__progress-action-checkbox {
								max-width: 100px;
								border-color: $color-dashboard-button-border !important;
								color: $color-dashboard-button-color;

								&:before {
									opacity: 0;
								}

								#{ $section_class }__progress-action-confirm-text {
									opacity: 1;
								}
							}

							#{ $section_class }__progress-action-cancel {
								display: inline-block;

								line-height: 1;

								padding: $dashboard-checkbox-padding;
								border: 1px solid $color-dashboard-button-border;
								color: $color-dashboard-button-color;
								border-radius: $dashboard-border-radius;

								cursor: pointer;
							}
						}
					}

					#{ $section_class }__name {
						width: 100%;
						display: inline-block;

						position: relative;

						padding-top: $dashboard-rows-vertical-padding;
						padding-bottom: $dashboard-rows-vertical-padding;

						a {
							width: 100%;
							display: block;

							position: relative;

							white-space: nowrap;
							overflow: hidden;
							text-overflow: ellipsis;
							
							color: inherit;
							text-decoration: none;

							outline: none;

							@include normal-transition( 'color' );

							&:hover {
								color: $color-url-hover;
							}

							/* Tablet and mobile */
							#{ $class }[max-width~="#{ $dashboard-medium-breakpoint }"] & {
								white-space: normal;
								overflow: visible;
								text-overflow: clip;
							}
						}
					}
				}

				#{ $section_class }__right {
					flex: 0 0 auto;
					display: flex;

					padding-right: $dashboard-space-between-elements;

					#{ $section_class }__action {
						padding-right: $dashboard-space-between-elements;
					}

					#{ $section_class }__progress {
						display: flex;
						flex-direction: column;

						font-size: $dashboard-progress-bar-font-size;
						color: $color-dashboard-progress-bar-font-color;

						&-sizer {
							height: 0px;
							pointer-events: none;
							visibility: hidden;
							position: relative;
							z-index: -1;
						}

						&-percentage {
							margin-bottom: 5px;
						}

						&-holder {
							width: 100%;
							height: $dashboard-progress-bar-height;
							background-color: $color-dashboard-progress-bar-holder-background;
							border-radius: 150px;

							position: relative;
							overflow: hidden;
						}

						&-bar {
							height: 100%;
							background-color: $color-dashboard-progress;

							position: absolute;
							top: 0px;
							left: 0px;
						}
					}

					/* Mobile */
					#{ $class }[max-width~="#{ $dashboard-small-breakpoint }"] & {
						width: 100%;
						padding-left: $dashboard-space-between-elements;
						padding-right: $dashboard-space-between-elements;
						padding-bottom: $dashboard-rows-vertical-padding;
					}
				}

				/* Mobile */
				#{ $class }[max-width~="#{ $dashboard-small-breakpoint }"] & {
					flex-wrap: wrap;
				}
			}
		}

		&:first-child &__row {
			border-top: 0;
		}

		&__content {
			display: none;
			padding-left: $dashboard-toggle-button-width - 1px;

			#{ $section_class }--expanded & {
				display: block;
			}

			/* Mobile */
			#{ $class }[max-width~="#{ $dashboard-small-breakpoint }"] & {
				padding-left: $dashboard-toggle-button-width-mobile - 1px;
			}
		}

		&__lessons,
		&__quizzes {
			border-left: 1px solid $color-dashboard-border;
		}
	}

