/**
 * Tooltip
 */

[ulg-tooltip-frontend] {
	position: absolute;
}

	[ulg-tooltip-frontend]::before,
	[ulg-tooltip-frontend]::after {
		text-transform: none;
		font-size: 13px;
		font-weight: 500;
		line-height: 1;
		user-select: none;
		pointer-events: none;
		position: absolute;
		display: none;
		opacity: 0;
	}

	[ulg-tooltip-frontend]::before {
		content: '';
		border: 5px solid transparent;
		z-index: 100001;
	}

	[ulg-tooltip-frontend]::after {
		width: 100%;
		content: attr(ulg-tooltip-frontend);
		text-align: center;
		min-width: 200px;
		max-width: 250px;
		/*white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;*/
		padding: 4px 10px 6px 10px;
		line-height: 1.4;
		border-radius: 4px;
		box-shadow: 0 1em 2em -.5em rgba(0, 0, 0, 0.35);
		background: #333;
		color: #fff;
		z-index: 100000;
	}

	[ulg-tooltip-frontend][ulg-flow-frontend^="left"]::after {
		min-width: 80px;
	}

	[ulg-tooltip-frontend]:hover::before,
	[ulg-tooltip-frontend]:hover::after {
		display: block;
	}

	[ulg-tooltip-frontend='']::before,
	[ulg-tooltip-frontend='']::after {
		display: none !important;
	}

	[ulg-tooltip-frontend]:not([ulg-flow-frontend])::before,
	[ulg-tooltip-frontend][ulg-flow-frontend^="up"]::before {
		bottom: calc(100% + 5px);
		border-bottom-width: 0;
		border-top-color: #333;
	}
	[ulg-tooltip-frontend]:not([ulg-flow-frontend])::after,
	[ulg-tooltip-frontend][ulg-flow-frontend^="up"]::after {
		bottom: calc(100% + 10px);
	}
	[ulg-tooltip-frontend]:not([ulg-flow-frontend])::before,
	[ulg-tooltip-frontend]:not([ulg-flow-frontend])::after,
	[ulg-tooltip-frontend][ulg-flow-frontend^="up"]::before,
	[ulg-tooltip-frontend][ulg-flow-frontend^="up"]::after {
		left: 50%;
		transform: translate(-50%, -.5em);
	}

	[ulg-tooltip-frontend][ulg-flow-frontend^="down"]::before {
		top: 100%;
		border-top-width: 0;
		border-bottom-color: #333;
	}
	[ulg-tooltip-frontend][ulg-flow-frontend^="down"]::after {
		top: calc(100% + 5px);
	}
	[ulg-tooltip-frontend][ulg-flow-frontend^="down"]::before,
	[ulg-tooltip-frontend][ulg-flow-frontend^="down"]::after {
		left: 50%;
		transform: translate(-50%, .5em);
	}
	[ulg-tooltip-frontend][ulg-flow-frontend^="left"]::before {
		top: 50%;
		border-right-width: 0;
		border-left-color: #333;
		left: calc(0em - 10px);
		transform: translate(-.5em, -50%);
	}
	[ulg-tooltip-frontend][ulg-flow-frontend^="left"]::after {
		top: 50%;
		right: calc(100% + 10px);
		transform: translate(-.5em, -50%);
	}
	[ulg-tooltip-frontend][ulg-flow-frontend^="right"]::before {
		top: 50%;
		border-left-width: 0;
		border-right-color: #333;
		right: calc(0em - 10px);
		transform: translate(.5em, -50%);
	}
	[ulg-tooltip-frontend][ulg-flow-frontend^="right"]::after {
		top: 50%;
		left: calc(100% + 10px);
		transform: translate(.5em, -50%);
	}

	[ulg-tooltip-frontend]:not([ulg-flow-frontend]):hover::before,
	[ulg-tooltip-frontend]:not([ulg-flow-frontend]):hover::after,
	[ulg-tooltip-frontend][ulg-flow-frontend^="up"]:hover::before,
	[ulg-tooltip-frontend][ulg-flow-frontend^="up"]:hover::after,
	[ulg-tooltip-frontend][ulg-flow-frontend^="down"]:hover::before,
	[ulg-tooltip-frontend][ulg-flow-frontend^="down"]:hover::after {
		animation: ulg-tooltip-frontends-vert 300ms ease-out forwards;
	}

	[ulg-tooltip-frontend][ulg-flow-frontend^="left"]:hover::before,
	[ulg-tooltip-frontend][ulg-flow-frontend^="left"]:hover::after,
	[ulg-tooltip-frontend][ulg-flow-frontend^="right"]:hover::before,
	[ulg-tooltip-frontend][ulg-flow-frontend^="right"]:hover::after {
		animation: ulg-tooltip-frontends-horz 300ms ease-out forwards;
	}

	@keyframes ulg-tooltip-frontends-vert {
		to {
			opacity: .9;
			transform: translate(-50%, 0);
		}
	}

	@keyframes ulg-tooltip-frontends-horz {
		to {
			opacity: .9;
			transform: translate(0, -50%);
		}
	}
